define(function () {
    BunsenBurner = function () {
        // in kJ/sec
        this.power = 1;
        this.lastCall = -1;
        this.startTime = new Date().getTime();
        this.timer = "0:00";
    };

    BunsenBurner.prototype.start = function () {
        var time = new Date().getTime();
        this.lastCall = time;
        this.startTime = time;
    };

    BunsenBurner.prototype.stop = function () {
        this.lastCall = -1;
        this.timer = "0:00";
    };

    /**
     * Returns the change in heat since last call
     * @param source Solution related to this object
     * @returns {number} Heat in kJ
     */
    BunsenBurner.prototype.getHeatSinceLastCall = function (source) {
        var time = new Date().getTime();
        var heat = (time - this.lastCall) / 1000 * this.power;
        this.lastCall = time;

        // update timer
        // https://stackoverflow.com/questions/19700283/how-to-convert-time-milliseconds-to-hours-min-sec-format-in-javascript/51155695
        var duration = time - this.startTime;
        var seconds = Math.floor((duration / 1000) % 60);
        seconds = (seconds < 10) ? "0" + seconds : seconds;
        var minutes = Math.floor((duration / (1000 * 60)) % 60);
        this.timer = minutes + ":" + seconds;
        return heat;
    };
    return BunsenBurner;
});
